@import url("https://fonts.googleapis.com/css?family=Lobster");
@import url(./assets/css/font-awesome.css);
@import url("https://fonts.googleapis.com/css?family=Arapey");
.primary {
  color: "blue!important"; }

.secondary {
  color: "red!important"; }

.third {
  color: "yellow!important"; }

body {
  font-family: "Lobster", sans-serif !important;
  background-color: #292621 !important;
  overflow-x: hidden;
  margin-top: 0 !important;
  position: fixed;
  top: 0 !important;
  font-size: 1.2em;
  font-weight: 100 !important; }

h1, h2, h3, h4, h5, h6 {
  font-family: "Lobster", sans-serif !important;
  text-align: center !important;
  font-weight: 100 !important; }

p, a {
  font-family: "Lobster", sans-serif !important;
  text-align: left;
  font-weight: 100 !important;
  font-size: 1.2em; }

#projectsSectionContent {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  grid-gap: 20px; }
  #projectsSectionContent img {
    width: 100%; }

.pageSection {
  width: 100%;
  background-color: #005cc3;
  padding-top: 110px;
  padding-left: 10vw;
  padding-right: 10vw;
  padding-bottom: 50px;
  min-height: 75vh;
  color: white; }

#contactSection {
  background-color: #3c5499;
  color: white;
  vertical-align: middle; }
  #contactSection h1 {
    padding-top: 40px;
    padding-bottom: 20px;
    text-align: center; }

#contactSection input {
  background-color: rgba(0, 0, 0, 0.4);
  border: none;
  border-radius: 8px; }

#contactSection textarea {
  background-color: rgba(0, 0, 0, 0.4);
  border: none;
  border-radius: 8px; }

#contactSend {
  background-color: white;
  border-radius: 8px;
  color: black; }

#page {
  max-width: 100vw;
  max-height: 100vh; }

#headerText {
  margin-top: 200px;
  text-align: center; }
  #headerText a {
    padding: 5px 60px;
    margin-top: 30px;
    background-color: #eb7d4b;
    border: none;
    color: white; }

#headerNavbar {
  padding-top: 30px;
  -webkit-box-shadow: 0 0 5px #000;
          box-shadow: 0 0 5px #000; }

.goog-te-gadget-simple {
  padding: 10px 20px;
  border-radius: 3px; }
  .goog-te-gadget-simple img {
    display: none !important; }

.goog-te-menu-value :last-child {
  display: none !important; }

.goog-te-menu-value :nth-child(3) {
  display: none !important; }

.goog-te-menu2-item .indicator {
  content: " " !important;
  display: block;
  background-image: url(http://icons.iconarchive.com/icons/icons-land/vista-flags/256/English-Language-Flag-1-icon.png) !important; }

.goog-te-gadget-simple {
  padding: 0;
  border-radius: 3px; }

.goog-te-gadget-simple {
  background-color: transparent !important;
  border: none; }

.goog-te-menu-value span {
  color: rgba(255, 255, 255, 0.7) !important; }

.goog-te-menu-frame {
  -webkit-box-shadow: none !important;
          box-shadow: none !important; }
  .goog-te-menu-frame .goog-te-menu2 {
    border: none !important; }

hr {
  margin: 25px 0; }

.goog-te-banner-frame {
  top: -80px !important;
  display: none !important;
  visibility: hidden !important;
  opacity: 0;
  z-index: 0; }

#google_translate_element {
  visibility: hidden;
  opacity: 0; }

#goog-gt-tt {
  top: -80px !important;
  display: none !important;
  visibility: hidden !important;
  opacity: 0;
  z-index: 0; }

/*slider styles*/
@-webkit-keyframes sliderPanning {
  0% {
    background-position: 0% 0%; }
  25% {
    background-position: 100% 100%; }
  50% {
    background-position: 100% 0%; }
  75% {
    background-position: 0 100%; }
  200% {
    background-position: 0 0; } }
@keyframes sliderPanning {
  0% {
    background-position: 0% 0%; }
  25% {
    background-position: 100% 100%; }
  50% {
    background-position: 100% 0%; }
  75% {
    background-position: 0 100%; }
  200% {
    background-position: 0 0; } }

.sliderItem {
  position: relative;
  height: 100%;
  width: 100%;
  background: black url(http://unsplash.it/1900/1900/?random&blur);
  background-size: 200%;
  -webkit-animation: panning 50s infinite linear;
          animation: panning 50s infinite linear; }



